.Core_Container {
  position: absolute;
  left: 50%;
  top: 0;
  transform: translate(-50%, 40%);
  z-index: 5;
}
.Core_Header {
  display: flex;
  justify-content: space-evenly;
  margin-top: 20px;
}
input {
  border: none;
  outline: none;
  border-bottom: 1px solid rgb(207, 207, 207);
  background: none;
  color: rgb(207, 207, 207);
}
::placeholder {
  color: rgb(207, 207, 207);
}
.titleActive {
  margin-top: 1px;
  border-bottom: 2px solid cyan;
}
.titleInactive {
  margin-top: 0px;
  border-bottom: 1px solid rgb(207, 207, 207);
}
.coreButton {
  border: none;
  width: 5vh;
  height: 5vh;
  background-size: cover;
  outline: none;
  transition-duration: 0.25s;
}
.hide {
  height: 30vh;
  width: 30vh;
  opacity: 0;
  pointer-events: none;
  transition-duration: 0.2s;
}
.unhide {
  height: 60vh;
  width: 60vh;
  position: absolute;
  background: rgb(99, 99, 99);
  pointer-events: fill;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  transition-duration: 0.2s;
  visibility: visible;
  pointer-events: fill;
  opacity: 1;
}
.Fullscreen {
  background-image: url("./images/fullscreen.svg");
  background-color: rgb(99, 99, 99);
}
.Exit {
  background-image: url("./images/exit.svg");
  background-color: rgb(99, 99, 99);
}
.coreButton:hover {
  border-radius: 50%;
  background-color: cyan;
}
textarea {
  margin-left: 50%;
  margin-top: 40%;
  padding: 5vh;
  padding-top: 2vh;
  width: 80%;
  height: 50%;
  outline: none;
  color: rgb(207, 207, 207);
  resize: none;
  transform: translate(-50%, -50%);
  background: none;
  border: none;
  border-top: 1px solid rgb(207, 207, 207);
}
