.App-header {
  position: absolute;
  z-index: 1;
  bottom: 0;
  width: 100%;
  height: 10vh;
  background: rgba(255, 255, 255, 0.808);
  justify-content: center;
  align-items: center;
  display: flex;
}
span {
  z-index: 301;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 2.5vh;
}
#passage_button {
  position: relative;
  outline: none;
  border: none;
  color: grey;
  /* height: 50px;
  width: 250px; */
  height: 7.5vh;
  width: 38.5vh;
  background-color: grey;
  cursor: pointer;
  transition: 0.25s;
  transition-property: opacity, transform;
}
#passage_button:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background: white;
  height: 7.5vh;
  width: 38.5vh;
  text-align: center;
  transition-duration: 0.25s;
  transition-property: opacity, transform;
}
#passage_button:hover::before {
  opacity: 0;
  transform: scaleX(0.25);
}
#passage_button:hover {
  color: white;
}
#passage_button:active {
  transform: scale(0.95, 0.95);
}
