.passage_index {
  position: absolute;
  height: 100px;
  width: 100px;
  padding: 2vh;
  transform: translate(-50%, -50%);
  background: rgba(66, 166, 266, 0.781);
  border: none;
  cursor: default;
  overflow: hidden;
}
.options {
  position: absolute;
  opacity: 0;
  z-index: 5;
  pointer-events: none;
  width: 17vh;
  height: 5vh;
  background: white;
  transform: translate(-50%, 12vh);
}
.options > button {
  object-fit: contain;
  border: none;
  width: 5vh;
  height: 5vh;
  background-size: cover;
  outline: none;
}
.delete_btn {
  background-image: url("./images/delete.svg");
}
.visible {
  pointer-events: fill;
  opacity: 1;
}
.empty_mode {
  clip-path: polygon(0 0, 0 100%, 35% 100%, 50% 100%, 100% 55%, 100% 0);
}
.passage_text {
  margin: 0;
  cursor: default;
  word-wrap: break-word;
}
.title {
  font-size: 16px;
}
.textile {
  font-weight: 500;
  font-size: 12px;
  word-wrap: break-word;
}

