.App {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  overflow: hidden;
}
.darken {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.534);
  z-index: 2;
  pointer-events: none;
}
